	import {SetPaperCollect,PaperDesc} from 'api/exam'
export default {
  name: "paperinfo",
  data() {
    return {
      classes: "未选择",
      //选中课程
      searchText: "",
      //搜索内容
      value: null,
      size: 9,
      readonly: true,
      paperInfo: {},
      //接口返回数组
      onshow: false,
      active: null,
      id: null,
      typeData: [],
      typeid: 0,
      paperid: 0,
      categoryid: 0,
      isPayPaper: false,
      collect: 1,
	  courseid:'',
	  workid:'',
	  stu:'',
	  state:{}
    };
  },
  methods: {
	open(item){
		uni.navigateTo({
			url:`/pages/exam/testpage?name=testpage&paperid=${this.paperid}&courseid=${this.courseid}&categoryid=${this.categoryid}&workid=${this.workid}&stu=${this.stu}&title=${item.title}`
		})
	},
    // 弹出层开关
    popup() {
      this.onshow = true;
    },
    newClass(classes) {
      this.classes = classes;
    },
    getPaperInfo() {
      
      this.onshow = false;
      PaperDesc({
        paperid: this.paperid,
        categoryid: this.categoryid
      }).then(res=> {
        if (res.result) {
          this.paperInfo = res.data;
          this.collect = res.data.collect;
        } else {
		  uni.showToast({
		  	title:res.msg,
			icon:'none'
		  })
        }
        
      });
    },
  

    /**收藏试卷 */
    collectionPaper () {
		this.collect ? this.collect = 0 : this.collect = 1;
		
     SetPaperCollect({
        type: 1,
        infoid: this.paperid,
        categoryid: this.categoryid,
        collect: this.collect
      }).then(res=> {
        if (res.result) {
			var tipstext = "";
			if (this.collect == 1) {
			  tipstext = "收藏成功";
			} else {
			  tipstext = "取消收藏";
			}
			uni.showToast({
				title:tipstext
			})
        } else {
			uni.showToast({
				title:res.msg,
				icon:'none'
			})
        }
      });
    }
  },
  onLoad(query) {
		console.log(query)
		this.categoryid = query.categoryid
		this.paperid = query.paperid
		this.courseid = query.courseid || ''
		this.workid = query.workid || ''
		this.stu = query.stu || ''
  },
  onShow() {
    this.$store.ready(state=> {
		this.state = state
		if (uni.getStorageSync('classes_'+state.userInfo.UserID)) {
		  this.classes = uni.getStorageSync('classes_'+state.userInfo.UserID);
		} 
		this.getPaperInfo();
	})
    
  }
};
